$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Animated Text
.wp-block-atb-animated-text {
	* {
		box-sizing: border-box;
	}

	.atbAnimatedText {
		.content {
			margin: 0;
		}

		p {
			margin: 0;
		}
	}
}